.container {
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.4);
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.detail {
    background-color: #31c5ff;
    max-width: 600px;
    width: 100%;
    border-radius: 0.4rem;
    box-shadow: 0 1px 10px black;
    min-height: 300px;
    display: flex;
    flex-direction: column;
    position: relative;
}

.title {
    text-align: center;
    border-bottom: 1px solid black;
}

.content {
    flex: 1;
}

.footer {
    padding: 1rem;
    border-top: 1px solid black;
    display: flex;
    justify-content: space-between;

    >button {
        font-size: 20px;
        border-radius: 10px;
    }

}

.btn-icon {
    justify-content: space-around;

}

.close {
    position: absolute;
    font-size: 2rem;
    font-weight: bold;
    top: 0.2rem;
    right: 1rem;
    cursor: pointer;
}

.btn1 {
    background: linear-gradient(45deg, #00b5ff, #005eff);
    border: 3px solid;
    font-weight: bolder;

}

.btn2 {
    background-color: #dddd;
    border-radius: 5px;
    padding: 6px;
}

.list {
    list-style: none;
    border: none;
    text-align: center;
}

.detail-img {
    height: 200px;
    width: 200px;
}

.addbutton {
    display: flex;
    justify-content: flex-start;
    margin-left: 20px;
    border-radius: 5px;
    padding: 6px;
    background-color: #dddd;
}

.btn2:hover {
    background: linear-gradient(45deg, #00b5ff, #005eff);
    transition: all 0.5s ease;
}


.addbutton:hover {
    background: linear-gradient(45deg, #00b5ff, #005eff);
    transition: all 0.5s ease;
}

.del-list {
    display: flex;
    justify-content: space-between;
    padding: 1px;
    margin-top: 10px;
    font-weight: bolder;
    text-align: left;
}