.log-container {
    display: flex;
    height: 90vh;
    width: 100%;
    justify-content: center;

}

.log-All {
    border: 2px solid black;
    justify-content: center;
    margin-top: 80px;
    border-radius: 20px;
    /* background-color: rgb(147, 126, 206); */

    max-width: 500px;
    max-height: 380px;
}



.log-taitel {
    display: flex;
    flex-direction: column;
    text-align: center;
    border-bottom: 2px solid black;
}


.log-name,
.log-email,
.log-password {
    margin: 20px;
    text-align: center;
    font-size: 30px;
    border-radius: 10px;

    >i {
        font-size: 25px;
        padding-right: 10px;
    }
}

.log-input {
    margin-top: 10px;
    border-radius: 5px;
    gap: 25px;
    font-size: 20px;
    background-color: #DDDDDD;
    padding: 5px;
    border: no;
}

.log-input:hover {
    box-shadow: 0px 0px 0px 5px #ff4d00;

}

.log-footer {
    padding: 30px;
    display: grid;
    border-top: 2px solid black;

}


.log-button-1 {
    justify-content: space-around;
    justify-content: space-between;
    border-radius: 10px;
    background-color: #DDDDDD;
    border: 2px solid black;
    text-decoration: none;
    color: black;
    padding: 10px;
    text-align: center;
    font-weight: bolder;
}

.log-button-2 {
    text-align: center;
    margin-top: 10px;
    font-weight: bolder;
    text-decoration: none;
    color: black;
}

.log-button-1:hover {
    background: linear-gradient(45deg, #00b5ff, #005eff);
    transition: all 0.5s ease;
}



.log-errormeg {
    font-weight: bolder;
    text-align: center;
}