.navbar {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 75px;
    color: black;
    background: linear-gradient(100deg, #ecc9f1, #31c5ff);
    height: 10vh;

    >h3 {
        font-size: 20px;

    }
}

.link {
    display: flex;
    justify-content: flex-end;
    list-style: none;
    width: 75%;
}

.home,
.product,
.about,
.signup {
    text-decoration: none;
    color: black;
    font-size: 20px;
    padding: 15px;
    margin: 25px;
}

.Mobileicon {
    display: none;
}

.home:hover {
    color: black;
    border-bottom: 3px solid #a23147;
}

.about:hover {
    color: black;
    border-bottom: 3px solid #a23147;
}

.product:hover {
    color: black;
    border-bottom: 3px solid #a23147;
}


.signup:hover {
    color: black;
    border-bottom: 3px solid #a23147;
}



.Mobileicon {
    display: none;
}

@media screen and (max-width:600px) {

    .Mobileicon {
        display: flex;
    }

    .title {
        display: flex;

    }

    .link {
        display: none;
    }

    .Mobilelink {
        position: absolute;
        display: block;
        list-style: none;
        background-color: #ddd;
        left: 0px;
        top: 75px;
        transition: all 0.5s ease-in;
        width: 92%;
        margin-top: -10PX;
        text-align: center;
        z-index: 1;
    }

    .Mobilelink li {
        margin: 25px;

    }

    .home,
    .about,
    .product,
    .signup {
        color: black;
        text-align: center;
        width: 100%;
        transition: all 0.5s ease;

    }

    .Mobileicon {
        display: block;
        position: absolute;
        font-size: 20px;
        color: white;
        background-color: black;
        outline: none;
        top: 15px;
        right: 25px;

    }
}