.cartcontainar {
    height: 90vh;
    width: 40%;
    border: 1px solid black;
    overflow: auto;
}

.cartcontainar::-webkit-scrollbar {
    display: none;
}

.Header {
    display: flex;
    background-color: #31c5ff;
    justify-content: space-between;
    align-items: center;
}


.taitle {
    padding: 10px;
}

.cart-icon {
    font-size: 30px;

}

.Bag-x {
    font-size: 20px;
    background-color: #31c5ff;
    /* background-color: hsla(294, 76%, 61%, 0.746); */
    font-weight: bolder;
}

.count {
    max-width: 35px;
    height: 20px;
    border: none;
    font-size: 30px;
    /* background-color: hsla(294, 76%, 61%, 0.746); */
    background-color: #31c5ff;

}

.qut {
    max-width: 35px;
    border: none;
    font-size: 30px;
    background-color: #31c5ff;
}

.cart-container {
    text-align: center;
    padding: 10px;

}

.newcart {
    display: flex;
    margin-top: 25px;
    justify-content: center;
    align-items: center;
    border: 2px solid black;
    border-radius: 20px;
    justify-content: space-evenly;
    padding: 10px 0px 10px 0px;
    font-weight: bolder;
    /* background-color: #31c5ff; */

}

.cart-leftside {
    display: grid;
    justify-content: center;
    align-items: center;

    >i {
        margin: 0;
        padding: 0;
        color: orangered;
        font-size: 15px;
    }

    >p {
        margin: 0;
        padding: 0;
    }
}

.cart-rating {
    >i {
        font-size: 15px;
    }
}

.cart-buttons {
    display: flex;
    justify-content: center;
    align-items: center;
}

.qut-buttons {
    display: flex;
    justify-content: center;
    align-items: center;
}

.totalchag {
    font-weight: bolder;
}

.footer-cartbox {
    border: 1px solid black;
    display: flex;
    gap: 40px;
    justify-content: flex-start;
    align-items: center;
    position: fixed;
    width: 40%;
    bottom: 0;
    background: linear-gradient(100deg, #ecc9f1, #31c5ff);
}


.order-button {
    font-size: 15px;
    font-weight: bolder;
    background-color: blue;
    color: white;
}

.order-button:hover {
    background-color: white;
    color: blue;
}

/* 
.qut-button {
} */

.qut-output {
    margin: 0;
    color: red;
    border: 1px solid black;
    padding: 0px 5px;
}

.cart-delete-button {
    border: none;

    >i {
        font-size: 20px;
        color: red;
    }
}

.cart-newimg {
    height: 50px;
}


@media screen and (max-width:600px) {
    .cartcontainar {
        width: 800px;
    }

    .footer-cartbox {
        width: 800px;
    }


}