.home-taitel-list {
    /* border: 1px solid red; */
    text-align: center;
    margin: auto;
    padding-top: 80px;
    margin-top: 20px;
    max-width: 300px;
    min-height: 440px;
    /* background-color: black; */
}


.home-advac1 {
    /* color: #a23147; */
    color: black;
    /* text-shadow: 4px 4px 4px black; */
    font-weight: 900;
    font-size: 20px;
}

.home-advac2 {
    /* color: red; */
    color: black;
    font-size: 50px;
}

.home-advac3 {
    font-weight: 900;
    color: black;
    /* text-shadow: 5px 5px 5px white; */
    font-size: 20px;
}

.home-advac4 {
    font-weight: bolder;
}

.home-buttons {
    margin-top: 40px;
}

.home-button {
    padding: 10px;
    border-radius: 10px;
    /* background-color: #a23147; */
    background-color: blue;
    color: white;
    font-weight: bolder;
    text-decoration: none;
}

.home-button:hover {
    box-shadow: 0px 0px 5px white,
        0px 0px 10px black inset;
    color: blue;
    background-color: white;

}