.sin-container {
    display: flex;
    justify-content: center;
    height: 90vh;
    width: 100%;
    border-color: white;
}

.sin-All {
    border: 2px solid black;
    justify-content: center;
    margin-top: 80px;
    border-radius: 20px;
    /* background: linear-gradient(100deg, #ecc9f1, #31c5ff); */
    max-width: 600px;
    max-height: 450px;
}

.sin-taitel {
    display: flex;
    flex-direction: column;
    text-align: center;
    /* gap: 10px; */
    border-bottom: 2px solid black;


}

.sin-name,
.sin-email,
.sin-password {
    margin: 20px;
    text-align: center;
    font-size: 30px;
    border-radius: 10px;

    >i {
        font-size: 25px;
        padding-right: 10px;
    }
}

.sin-input {
    margin-top: 10px;
    border-radius: 5px;
    gap: 25px;
    font-size: 20px;
    background-color: #DDDDDD;
    padding: 5px;
}

.sin-input:hover {
    box-shadow: 0px 0px 0px 5px #ff4d00;
}

.sin-footer {
    padding: 20px;
    display: grid;
    border-top: 2px solid black;
}


.sin-button-1 {
    border-radius: 10px;
    background-color: #DDDDDD;
    border: 2px solid black;
    text-decoration: none;
    color: black;
    padding: 10px;
    text-align: center;
    font-weight: bolder;

}

.sin-button-2 {
    color: black;
}


.sin-p {
    text-align: center;
    font-weight: bolder;
}

.sin-button-1:hover {
    /* background-color: #66BFBF; */
    background: linear-gradient(45deg, #00b5ff, #005eff);
    transition: all 0.5s ease;
}

.sin-errormeg {
    font-weight: bolder;
    text-align: center;
}