.Admin-product-All {
    margin: 10;
}

.Admin-product-list {
    top: 65px;
    text-align: center;

    >thead {
        padding: 5px;
    }
}

.Admin-tabel-list {
    background-color: #ddd;
    border-bottom: 2px soild black;

    >td {
        font-weight: bolder;
    }
}


.tabel-heater th {
    background-color: #31c5ff;
    padding: 20px 0px 20px 0px;
}

.Admin-taitel-name {
    width: 100%;
    list-style: none;
    border: 2px solid black;
    justify-content: space-around;
}

.Admin-productimg {
    width: 120px;
    height: 80px;
}

.tabel-buttons {
    margin-top: 10px;
    gap: 10px;
    display: grid;
    justify-content: center
}

.edit-but {
    font-size: 15px;
    background-color: #31c5ff;
    font-weight: bold;
}

.delete-but {
    font-size: 15px;
    background-color: #31c5ff;
    font-weight: bold;
}

