.Admin-container-All {
    height: 90vh;
    width: 100%;
    display: flex;
    background: linear-gradient(250deg, hsla(294, 97%, 56%, 0.746), #2e79f9f9);
}

.Admin-container-right {
    height: 89vh;
    width: 30%;
    background-color: #ddd;
    border: 2px solid black;
}

.Admin-taitel-1 {
    text-align: center;
    border: 2px solid black;
    background-color: rgb(0, 140, 255);
}

.Admin-pagelink-All {
    margin-top: 50px;
    display: grid;
}



.Admin-product-link {
    text-decoration: none;
    color: black;
    font-weight: 900;
    margin-left: 30%;
}

.Admin-but-1 {
    background-color: #ddd;
    border: none;
}

.Admin-container-left-all {
    width: 70%;
}

.Admin-container-left-produ {
    height: 90vh;
    overflow: auto;
}

.Admin-container-left-produ::-webkit-scrollbar {
    display: none;
}