.Admin-detail-All {
    border: 2px solid #000000;
    box-shadow: 0px 0px 0px 5px black;
    margin: 10%;
    margin-left: 15%;
    border-radius: 25px;
    width: 70%;
    background: linear-gradient(250deg, hsla(294, 97%, 56%, 0.746), #2e79f9f9);

}

.Admin-taitel-2 {
    text-align: center;
    border-bottom: 2px solid black;
}

.Admin-detail-input {
    display: flex;
    margin-top: 5px;
    padding: 15px;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 20px;
}

.Admin-Input {
    border: 2px solid black;
    /* gap: 20px; */
    padding: 15px;
    border-radius: 10px;
    background-color: #DDDDDD;
    /* font-size: small; */
    font-weight: bold;
    text-align: center;
}

.Admin-Input:focus {
    outline-color: #005eff;
    background-color: #DDDDDD;
    color: black;
    box-shadow: 5px 5px black;
}

.Admin-buttons {
    margin-top: 10px;
    text-align: center;
    border-top: 2px solid black;
}

.Admin-button-1 {
    margin: 10px;
    font-size: 25px;
    border-radius: 10px;
}



.Admin-img-input {
    text-align: center;
    margin-right: 18px;
    border-radius: 5px;
    font-size: 25px;
}

.Admin-img-list {
    display: flex;
    margin-top: 20px;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.taitelname {
    font-weight: 900;
}

.Admin-description {
    display: flex;
    margin-top: 40px;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.Admin-descripton-input {
    font-size: 20px;
}