.containar {
    display: flex;
    height: 90vh;
    width: 100%;
    background-color: white;
}

.productall {
    height: 100%;
    width: 100%;
    overflow: auto;
}

.productall::-webkit-scrollbar {
    display: none;
}

.search {
    text-align: center;
}

.input {
    border-radius: 20px 0px 0px 20px;
    font-size: 30px;
}

::placeholder {
    color: black;
}

.input:hover {
    background-color: white;
    transition: all 0.5s ease;
    color: black;
}

.button {
    border-radius: 0px 20px 20px 0px;
    /* background-color: rgb(162, 112, 212); */
    font-size: 29px;
}

