.products {
    margin-top: 30px;
    text-align: center;
    margin-left: -35px;
}

.productimg {
    width: 120px;
    height: 110px;
    filter: drop-shadow(15px 15px 15px);
}

.ProductHome-list {
    align-items: center;
    border-radius: 20px;
    margin: 8px;
    padding: 0px 5px 7px 5px;
    font-weight: 600;
    border: 1px solid black;
}

.ProductHome-list:hover {
    color: black;
    background-color: #56c2ec;
}

.ul {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    list-style: none;
    gap: 10px;
}

.offer {
    text-decoration: line-through;
}

.rating {
    font-size: 15px;
    border-radius: 10px;
}

.product-viewbutton {
    border-radius: 8px;
    font-size: 20px;
    background-color: blue;
    color: white;
    /* background-color: #00b5ff; */
}


.product-viewbutton:hover {
    color: blue;
    background-color: white;
}

.Product-buttons {
    display: flex;
    justify-content: space-around;
}

.Addbuttonc {
    border-radius: 8px;
    font-size: 20px;
    background-color: blue;
    color: white;
    /* background-color: #00b5ff; */
}


.Addbuttonc:hover {
    color: blue;
    background-color: white;
}



.cardline1 {
    border-bottom: 2px solid black;
    text-align: center;
    margin: 8px;
    padding-bottom: 20px;

}


.Bag {
    border-radius: 5px;
    margin: 10px;
    font-size: 25px;
    border: none;
}

.count-Bag {
    max-width: 50px;
    height: 20px;
    border: none;
    font-size: 30px;
}




@media screen and (max-width:600px) {
    .ul {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));

    }

    .search {
        align-items: center;
        display: flex;
        justify-content: center;
        width: 100%;
    }

    .input {
        width: 70%;
    }
}